            .app_item_5 {
                margin-top: 19px;
                background: url(../img/slids/6-layers.png) center center/cover no-repeat; }
            .app_item_6 {
                margin-top: 19px;
                background: url(../img/slids/6-layers.png) center center/cover no-repeat; } } } }

.choise {
    min-height: 1000px;
